import { formatByGroups } from 'lib-bb-iban';
import getStyle from 'lib-bb-styles';
import {
  defaultTemplateView,
  tableHeaders,
  cardsSortingData,
  CSS_SELECTORS,
  CHART_OPTIONS,
 } from './constants';

const helpers = (context) => {
  const dateFilter = context.$filter('date');
  const i18nFilter = context.$filter('i18n');

  /**
   * @description
   * Table headers configuration
   * @type {Array}
   * @name headers
   */
  const headers = tableHeaders;

  /**
   * @description
   * Cards configuration for sorting
   * @type {Array}
   * @name cardsSort
   */
  const cardsSorting = cardsSortingData;

  /**
   * @description
   * Checks if the sortable column is the current one, table is sorted by the
   * active column
   * @name isSortableActive
   * @type {function}
   * @param  {object} sortable Sortable configuration
   * @param  {object} $ctrl    Current controller
   * @return {boolean}         True if table is sorted by the column, false otherwise
   */
  const isSortableActive = (sortable, $ctrl) => sortable.key === $ctrl.state.accounts.orderBy;

  /**
   * @description
   * Returns the account number with fallbacks
   * @name getAccountNumber
   * @type {function}
   * @param  {object} item Account
   * @return {string}      Acoount number if any
   */
  const getAccountNumber = item => formatByGroups(item.IBAN)
    || formatByGroups(item.BBAN) || item.productNumber;

  /**
   * @description
   * Checks if actual pagination type matches the one, defined in properties
   *
   * @public
   * @name isPaginationTypeMatch
   * @type {function}
   * @param {function} $ctrl      Current controller
   * @param {string} type         Description of pagination type (pagination, load-more)
   *
   * @returns {boolean}
   */
  const isPaginationTypeMatch = ($ctrl, type) =>
    $ctrl.state.accounts.paginationType === type;

  /**
   * @description
   * Gets the accounts collection from controller state
   * @name getAccounts
   * @type {function}
   * @param  {object} $ctrl Current controller
   * @return {array}        Accounts collection
   */
  const getAccounts = ($ctrl) => $ctrl.state.accounts.data;

  /**
   * @description
   * Opens the modal window with selectedProduct
   * @name openModalWindow
   * @type {function}
   * @param {object} ext                extension reference
   * @param {object} accounts           accounts state reference
   * @param {object} selectedProduct    product to be shown
   */
  const openModalWindow = (ext, accounts, selectedProduct) => {
    Object.assign(ext, { showModal: true, accounts, product: selectedProduct });
  };

  /**
   * @description
   * Closes the modal window
   * @name closeModalWindow
   * @type {function}
   * @param {object} ext extension reference
   */
  const closeModalWindow = (ext) => {
    Object.assign(ext, { showModal: false, product: null });
  };

  const productTypeTemplateNameMap = {
    'credit-card': 'credit-card',
    'debit-card': 'debit-card',
    'investment-account': 'investment',
    loan: 'loan',
    'current-account': 'current',
    'savings-account': 'savings',
    'term-deposit': 'term-deposit',
  };

  /**
   * @description
   * Generates account details template URL
   * @name getProductTypeTemplateId
   * @type {function}
   * @param  {string} type The account type
   * @return {string}      Generated template URL
   */
  const getProductTypeTemplateId = (type) => {
    const templateName =
      productTypeTemplateNameMap[type.trim().toLowerCase().split(' ').join('-')];
    return `accounts-overview-details/${templateName}.html`;
  };

  /**
   * @description
   * Transforms a boolean into human readable format
   * @name formatBooleanToText
   * @type {function}
   * @param  {boolean} value The boolean value
   * @return {string}        Formatted booelan
   */
  const formatBooleanToText = (value) => (value ? 'yes' : 'no');

  const formatAccountNumber = formatByGroups;

  /**
   * @description
   * Check if value is defined, not null and not empty
   * @name hasValue
   * @type {function}
   * @param  {*} value  The value to check
   * @return {boolean}
   */
  const hasValue = (value) => typeof value !== 'undefined' && value !== null && value !== '';

  const frequencyUnitKeyPrefix = 'accounts.detail.frequency';
  const frequencyUnits = {
    D: `${frequencyUnitKeyPrefix}.day`,
    W: `${frequencyUnitKeyPrefix}.week`,
    M: `${frequencyUnitKeyPrefix}.month`,
    Y: `${frequencyUnitKeyPrefix}.year`,
  };
  /**
   * @description
   * Gets the translation key giving a frequency unit identifier
   * @name getFrequencyUnitKey
   * @type {function}
   * @param  {string} unit  Short representation of frequency unit.
   *                        Possbile values are:
   *                        D - day
   *                        W - week
   *                        M - month
   *                        Y - year
   * @return {string} Translation key label
   */
  const getFrequencyUnitKey = (unit) => frequencyUnits[unit] || '';

  /**
   * @description
   * Checks if it is an array and it is empty
   * @name isEmptyArray
   * @type {function}
   * @param {*} value The value to check
   * @return {boolean}
   */
  const isEmptyArray = (value) => !(Array.isArray(value) && Boolean(value.length));

  /**
   * @description
   * Template view type, what will be the view Card or List
   * @type {Object}
   * @param {string} type extension template type ( Card, List )
   */
  const templateViewType = { type: defaultTemplateView };

  /**
   * @description
   * Change template view type
   * @name changeTemplateTypeView
   * @type {function}
   * @param {string} view what type of template is, 'card' or 'list'
   *
   */
  const changeTemplateTypeView = (view) => {
    templateViewType.type = (view || defaultTemplateView);
  };

  /**
   * @description
   * Changing sort direction by asc or desc. On second click on same sort type,
   * the sorting is reversed (ASC/DESC)
   *
   * @name changeSortCardDirection
   * @type {function}
   * @param  {string} sortBy  what sortable key/type to be changed direction
   *
   * */
  const changeSortCardDirection = (sortBy) => {
    const cardsSortByData = cardsSorting.find((item) => item.sortable.key === sortBy);
    cardsSortByData.sortable.direction =
      cardsSortByData.sortable.direction === 'ASC' ? 'DESC' : 'ASC';
  };

  /**
   * @description
   * Configuration of balance history chart
   *
   * @inner
   * @name lineChartOptionsInner
   * @type {object}
   * @returns {object} Configuration of performance chart
   */
  const lineChartOptionsInner = {
    legend: {
      display: false,
    },
    tooltips: {
      enabled: true,
      displayColors: false,
      callbacks: {
        title() {
        },
        label(tooltipItem) {
          return `(${tooltipItem.yLabel})  ${dateFilter(tooltipItem.xLabel, 'mediumDate')}`;
        },
      },
    },
    scales: {
      ticks: {
        display: false,
      },
      yAxes: [{
        gridLines: {
          color: getStyle(CSS_SELECTORS.yAxesgridLinescolor, 'color'),
          tickMarkLength: CHART_OPTIONS.tickMarkLength,
          zeroLineColor: getStyle(CSS_SELECTORS.yAxisZeroColor, 'color'),
          zeroLineWidth: CHART_OPTIONS.zeroLineWidth,
        },
        ticks: {
          display: false,
        },
      }],
      xAxes: [{
        display: false,
      }],
    },
    layout: {
      padding: CHART_OPTIONS.paddings,
    },
  };

  /**
   * @description
   * Handles one or multiple errors messages, mapping error key with propper message
   *
   * @name handleErrors
   * @type {function}
   * @param {array} errors
   * @returns {string} message
   */
  const handleErrors = (errors) => {
    if (!errors) {
      return null;
    }
    return errors.map((e) => i18nFilter(e)).join(' ');
  };

  /**
   * @description
   * Configuration of balance history chart
   *
   * @public
   * @name lineChartOptions
   * @type {function}
   * @returns {object} Configuration of performance chart
   */
  const lineChartOptions = (currency) => {
    lineChartOptionsInner.tooltips.callbacks.label = (tooltipItem) =>
    // eslint-disable-next-line max-len
      `(${currency} ${tooltipItem.yLabel.toFixed(2)}) ${dateFilter(tooltipItem.xLabel, 'short')}`;
    return lineChartOptionsInner;
  };

  return {
    headers,
    cardsSorting,
    isSortableActive,
    getAccountNumber,
    isPaginationTypeMatch,
    getAccounts,
    openModalWindow,
    closeModalWindow,
    getProductTypeTemplateId,
    formatBooleanToText,
    formatAccountNumber,
    hasValue,
    getFrequencyUnitKey,
    isEmptyArray,
    templateViewType,
    changeTemplateTypeView,
    changeSortCardDirection,
    lineChartOptions,
    handleErrors,
  };
};

export default helpers;



